.NavigationItem {
  margin: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  width: 100%;
}

.NavigationItem a {
  padding: 0.5rem 1rem;
  color: #020200;
  /* color: #8f5c2c; */
  text-decoration: none;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: var(--gold);
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: auto;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
  }
  .NavigationItem a {
    color: #020200;
    padding: 0.5rem 1rem;
  }
  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    color: var(--gold);
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
  }
}



