.Toolbar {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 90;
  overflow: hidden;
  border-bottom: 5px solid var(--gold);
}
.Toolbar h1 {
  font-family: var(--oswald);
  font-weight: normal;
  color: var(--gold);
  margin: 0;
}
.Toolbar nav {
  height: 100%;
}

.Logo {
  height: 80%;
}

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}



